.btn, .select, .image-input {
  background-color: #444;
  border: none;
  border-radius: 2px;
  color: #bbb; }

body {
  background-color: #333; }

* {
  box-sizing: border-box; }

.title {
  margin-top: 12px;
  margin-bottom: 12px;
  text-align: center;
  color: #bbb;
  font-size: 48px;
  font-weight: 200; }

.dropbox {
  position: relative;
  display: block;
  width: 600px;
  height: 200px;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid #444;
  border-radius: 4px;
  background-color: #222;
  color: #bbb;
  font-size: 24px;
  text-align: center; }
  .dropbox.over {
    background-color: #2f2f2f; }

.progress {
  position: absolute;
  top: 4px;
  left: 0;
  width: 100%;
  padding-left: 4px;
  padding-right: 4px; }

.label-title {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  transition: opacity 0.4s;
  user-select: none; }
  .label-title.mask {
    opacity: .2; }

.info-msg {
  font-size: 16px; }

.progress-label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  font-size: 10px;
  text-align: center; }

.progress-bar {
  display: none;
  vertical-align: top;
  appearance: none;
  width: 100%;
  height: 12px; }
  .progress-bar::-webkit-progress-bar {
    background-color: inherit; }
  .progress-bar::-webkit-progress-value {
    background-color: #444; }
  .progress-bar.show {
    display: inline-block; }

.image-select-input {
  display: none; }

.btn-container {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 4px;
  text-align: center; }

.btn {
  display: none;
  padding: 2px 10px;
  border: 1px solid #444;
  line-height: 1;
  font-size: 18px; }
  .btn:hover {
    border-color: #333; }
  .btn:active {
    transform: scale(0.96); }
  .btn.show {
    display: inline-block; }

.selections {
  position: relative;
  background-color: #222;
  border: 2px solid #444;
  width: 500px;
  margin: 0 auto;
  margin-top: 8px;
  padding: 8px;
  border-radius: 4px;
  color: #bbb; }

.selections-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  visibility: collapse;
  transition: all 0.4s; }
  .selections-mask.mask {
    visibility: visible;
    background-color: rgba(34, 34, 34, 0.8); }

.checkbox-container {
  float: right; }

.checkbox {
  vertical-align: middle; }

.label-container {
  margin-top: 8px;
  text-align: center; }

.dimension-label {
  display: inline-block;
  width: calc(50% - 8px); }

.dimension-inputs {
  margin-top: 2px;
  text-align: center;
  width: 100%; }

.input-container {
  display: inline-block;
  width: calc(50% - 6px); }
  .input-container:first-child {
    margin-right: 6px; }

.image-input {
  width: 100%;
  margin-bottom: 4px;
  padding: 3px 4px 2px 4px; }
  .image-input:last-child {
    margin-bottom: 0; }
  .image-input.invalid {
    animation: shake 0.4s; }

@keyframes shake {
  0% {
    transform: scale(1.02) rotate(1deg); }
  33% {
    transform: scale(1.02) rotate(-1deg); }
  66% {
    transform: scale(1.02) rotate(1deg); }
  100% {
    transform: scale(1) rotate(0); } }
